<template lang="html">
  <div class="Institutional-info">
    <Row>
      <Col  class="Institutional-info-title">{{$t("common.Institutional_basic_information")}}</Col>
    </Row>
    <table>
      <colgroup></colgroup>
      <tbody>
        <tr>
          <th>{{$t("common.Institution_name")}}</th>
          <td>{{msg.name}}</td>
          <th>{{$t("common.Institution_number")}}</th>
          <td>{{msg.user_number}}</td>
        </tr>
        <tr>
          <th>{{$t("common.Registered_address")}}</th>
          <td>{{msg.address}}</td>
          <th>{{$t("common.Company_address")}}</th>
          <td>{{msg.address}}</td>
        </tr>
        <tr>
          <th>{{$t("common.License")}}</th>
          <td> <a href="javascript:;">{{$t("common.View")}}</a> </td>
          <th>{{$t("common.Legal_person_ID")}}</th>
          <td><a href="javascript:;">{{$t("common.View")}}</a></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data (){
    return {
      msg:{
        SID: '',
        user_number: '',
        name: '',
        address: '',
        source:'',
        date:'',
        login_time:''
      }
    }
  },
  methods:{
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.msg = routerParams
        }
    }
  },
  mounted(){
    this.getParams()
  }
}
</script>

<style lang="css">
.Institutional-info{
  padding-top: 70px;
  width: 1200px;
  margin: 0 auto;
}
.Institutional-info table{
  width: 100%;
  border-collapse:collapse;
}

.Institutional-info table tr:nth-child(odd){
  background-color:#fff;
}
.Institutional-info table th{
  width: 120px;
  height: 30px;
  text-align: right;
  padding-right: 5px;
  border: 1px solid #DDDEE1;
   background-color:#fff;
}
.Institutional-info table td{
  width: 200px;
  height: 30px;
  text-align: left;
  padding-left: 5px;
  border: 1px solid #DDDEE1;
   background-color:#fff;
}
.Institutional-info table td a{
  color: #2FA4E7;
}
.Institutional-info .Institutional-info-title{
  text-align: left;
  padding-left: 30px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  background: #fff;
  margin-bottom: 20px;
}
</style>



